import PropTypes from 'prop-types'
import React, { PureComponent } from 'react'
import Helmet from 'react-helmet'
import avatar from '../assets/images/logo.png'

export default class SEO extends PureComponent {
  static propTypes = {
    title: PropTypes.string,
    image: PropTypes.string,
    url: PropTypes.string,
    description: PropTypes.string,
    isPost: PropTypes.bool,
  }

  static defaultProps = {
    title: 'Muhammad Fahri',
    image: `${avatar}`,
    url: 'https://muhammadfahri.me/',
    description: 'Muhammad Fahri - Backend Developer',
    isPost: false,
  }

  render() {
    const { image, url, description, isPost } = this.props
    const title = (this.props.title === '' ? '' : `${this.props.title} Â· `) + 'Muhammad Fahri'
    const schemaOrgJSONLD = [
      {
        '@context': 'http://schema.org',
        '@type': 'WebSite',
        sameAs: [
          'https://twitter.com/muhammdfahrim',
          'https://www.facebook.com/muhammdfahrim',
          'https://www.instagram.com/muhammadfahrim_',
          'https://www.github.com/gh0zialfat1h',
          'https://www.linkedin.com/in/muhammad-fahri-60038716a/',
        ],
        url: url,
        name: title,
        alternateName: description,
      },
    ]

    if (isPost) {
      schemaOrgJSONLD.push([
        {
          '@context': 'http://schema.org',
          '@type': 'BreadcrumbList',
          sameAs: [
            'https://twitter.com/muhammdfahrim',
            'https://www.facebook.com/muhammdfahrim',
            'https://www.instagram.com/muhammadfahrim_',
            'https://www.github.com/gh0zialfat1h',
            'https://www.linkedin.com/in/muhammad-fahri-60038716a/',
          ],
          itemListElement: [
            {
              '@type': 'ListItem',
              position: 1,
              item: {
                '@id': url,
                name: title,
                image: image,
              },
            },
          ],
        },
        {
          '@context': 'http://schema.org',
          '@type': 'BlogPosting',
          sameAs: [
            'https://twitter.com/muhammdfahrim',
            'https://www.facebook.com/muhammdfahrim',
            'https://www.instagram.com/muhammadfahrim_',
            'https://www.github.com/gh0zialfat1h',
            'https://www.linkedin.com/in/muhammad-fahri-60038716a/',
          ],
          url: url,
          name: title,
          alternateName: `${url} | Muhammad Fahri`,
          headline: title,
          image: {
            '@type': 'ImageObject',
            url: image,
          },
          description,
        },
      ])
    }
    return (
      <Helmet>
        <title>{title}</title>
        <meta name="description" content={description} />
        <meta name="image" content={image} />
        <script type="application/ld+json">{JSON.stringify(schemaOrgJSONLD)}</script>

        <meta property="fb:app_id" content="2362615167377525" />
        <meta property="og:url" content={url} />
        <meta property="og:type" content={isPost ? 'article' : 'website'} />
        <meta property="og:title" content={title} />
        <meta property="og:description" content={description} />
        <meta property="og:image" content={image} />

        <meta name="twitter:card" content="summary_large_image" />
        <meta name="twitter:site" content={url} />
        <meta name="twitter:creator" content="@muhammdfahrim" />
        <meta name="twitter:title" content={title} />
        <meta name="twitter:description" content={description} />
        <meta name="twitter:image" content={image} />
      </Helmet>
    )
  }
}
